import FlexibleDescription from "../vendor/flexible-description.min";

export default class Article {
  constructor() {
    window["FlexibleDescription"] = FlexibleDescription;
    this.init();
  }

  init() {
    this.setFlexibleDescription();
    $(".article-box.layout-6").map((index, el) =>
      this.setLayout6AutoHeightTitle($(el))
    );
  }

  setLayout6AutoHeightTitle(el) {
    this.calcLayout6TitleHeight(el);
    //  绑定resize监听
    let resize = () => {
      this.calcLayout6TitleHeight(el);
    };

    $(window).on("resize", this.debounce(resize, 200));
  }

  calcLayout6TitleHeight(el) {
    let titles = el.find(".article-card-section-title");
    //  计算出最高的
    let maxHeight = 0;
    titles.map((index, title) => {
      if ($(title).outerHeight() > maxHeight) {
        maxHeight = $(title).outerHeight();
      }
    });
    //  给所有title设置高度一致
    titles.map((index, title) => {
      $(title).css("min-height", `${maxHeight}px`);
    });
  }

  debounce(fn, delay) {
    delay = delay || 100;
    return () => {
      let args = Array.prototype.slice.apply(arguments);
      clearTimeout(fn.debounceTimeout);
      fn.debounceTimeout = setTimeout(() => {
        fn.apply(window, args);
      }, delay);
    };
  }

  setFlexibleDescription() {
    /*layout-3*/
    new FlexibleDescription({
      selector: ".article-box.layout-3 .article-card-meta",
      rows: 99,
      titleSelector: ".article-card-title",
      descriptionSelector: ".article-card-description",
      responsive: [
        {
          breakpoint: 300,
          rows: 3,
        },
        {
          breakpoint: 768,
          rows: 4,
        },
        {
          breakpoint: 992,
          rows: 3,
        },
        {
          breakpoint: 1248,
          rows: 4,
        },
      ],
    });

    /*layout-8*/
    new FlexibleDescription({
      selector: ".article-box.layout-8 .article-card-meta",
      rows: 99,
      titleSelector: ".article-card-title",
      descriptionSelector: ".article-card-description",
      responsive: [
        {
          breakpoint: 300,
          rows: 3,
        },
        {
          breakpoint: 768,
          rows: 3,
        },
        {
          breakpoint: 992,
          rows: 4,
        },
        {
          breakpoint: 1248,
          rows: 4,
        },
      ],
    });
  }
}
