import VAudio from "../vendor/v-audio.min";
import Range from "../vendor/range.min";
export default class AudioPlayer {
  constructor() {
    this.isFirstTimeMute = true;
    this.isMute = false;
    this.newPercentage = 0;
    this.init();
  }
  init() {
    this.init_element();

    this.init_vAudio();

    this.init_audio_range();

    this.init_volume_range();

    this.close_fix_player();

    this.on_scroll();
  }

  init_element() {
    this.$fixPlayer = $("#fix-player");
    this.$fixPlayerClose = $("#fix-player .btn-icon-close");
    this.durationSeconds = 0;
    this.isFirstPlayer = true;
  }

  //  ios设备, 移动端不显示音量条(这个在ios设备上是失效的, 请参考soundcloud)
  hide_volume_bar_on_ios() {
    $(".audio-player").addClass("ios");
  }

  //  初始化vAudio播放器
  init_vAudio() {
    let _this = this;
    let audioUrl = $(".audio-player").data("url");
    this.V = new VAudio({
      src: audioUrl,
      autoPlay: false,
    });

    //  歌曲加载完后, 更新时间码
    this.V.on("loaded", (ev, currentTime, duration) => {
      $(".current-time").html(this.V.format_time(currentTime));
      $(".duration").html(this.V.format_time(duration));
      this.durationSeconds = duration;

      $(".audio-player").removeClass("loading");
    });

    this.V.on(
      "watcherUpdate",
      (event, currentTime, percentage, duration, ended) => {
        if (!this.isSwitch) {
          //  更新range对象
          this.audioRange.set_value(percentage, true);
          //  更新时间吗
          $(".current-time").html(this.V.format_time(currentTime));
        }
      }
    )
      .on("ended", () => {
        this.V.set_current_time(0);
        this.V.play();
      })
      .on("play", () => {
        $(".play-pause").addClass("playing");

        let animationName =
          window.innerWidth >= 768 ? "slide-right-PC" : "slide-right-MB";
        _this.$fixPlayer.removeClass().addClass(animationName);
      })
      .on("pause", () => {
        $(".play-pause").removeClass("playing");
      });

    //  按钮绑定
    $(".play-pause").on("click", (ev) => {
      let $this = $(ev.currentTarget);

      //  判断第一次点击播放按钮时不显示关闭按钮
      if (this.isFirstPlayer) {
        this.isFirstPlayer = false;
      } else {
        this.$fixPlayerClose.toggle();
        this.$fixPlayerClose.find(".icon-close").removeClass("hidden");
        this.$fixPlayerClose.find(".icon-navigate-next").addClass("hidden");
        if (this.$fixPlayerClose.attr("style") !== "") {
          this.isTicking = true;
        } else {
          this.isTicking = false;
        }
      }
      if ($this.hasClass("playing")) {
        this.V.pause();
      } else {
        this.V.play();
      }
    });
  }

  //  初始化音频进度条
  init_audio_range() {
    let $this = this;
    this.audioRange = new Range({
      el: "#range-target",
      value: 0,
      min: 0,
      max: 1000,
    });

    //  拖拽range更新音频
    //  使用isSwitch避免用户拖拽滑杆时频繁更新音频
    this.isSwitch = false;
    this.audioRange.on("changeStart", () => (this.isSwitch = true));
    this.audioRange.on("changeEnded", () => {
      this.isSwitch = false;
      this.V.set_current_time(this.newPercentage, true);
      this.V.play();
    });
    this.audioRange.on(
      "slideChange",
      (event, value, percentage, isManually) => {
        //  更新音频
        if (isManually) return;
        if (!this.isSwitch) {
          this.V.set_current_time(percentage, true);
        } else {
          this.newPercentage = percentage;
        }
        //  更新时间码, 实际开发中, 先保存duration, 不要频繁调用
        $(".current-time").html(
          this.V.format_time($this.durationSeconds * percentage)
        );
      }
    );
  }

  //  初始化音量进度条
  init_volume_range() {
    this.volumeRange = new Range({
      el: "#volume-range-target",
      value: 50,
      min: 0,
      max: 100,
    });

    $(".volume-box .btn-icon").on("click", (ev) => {
      let $el = $(ev.currentTarget);
      if ($el.index()) {
        //  最大音量
        this.volumeRange.set_value(100);
        this.V.set_volume(1);
        this.isMute = false;
      } else {
        //  处理进入后直接点静音按钮, 导致safari浏览器播放后, 还是有声音的问题
        //  原因是有二, 1. 音频还未加载, 2. 音频还未播放, 导致直接静音失效
        if (this.isFirstTimeMute) {
          this.isFirstTimeMute = false;
          this.V.one("play", () => {
            this.isMute && this.V.mute();
          });
        }
        //  静音
        this.V.mute();
        this.volumeRange.set_value(0);
        this.isMute = true;
      }
    });

    //  使用isSwitch避免用户拖拽滑杆时频繁更新音量
    let isSwitch = false;

    this.volumeRange.on("changeStart", () => (isSwitch = true));
    this.volumeRange.on("changeEnded", () => (isSwitch = false));
    this.volumeRange.on(
      "slideChange",
      (event, value, percentage, isManually) => {
        //  更新音量
        !isSwitch && !isManually && this.V.set_volume(percentage);

        if (percentage == 0) {
          this.volumeRange.set_value(0);
          this.V.mute();
        } else {
          this.V.set_volume(percentage);
        }
      }
    );
  }

  //  关闭悬浮播放器
  close_fix_player() {
    this.$fixPlayerClose.on("click", () => {
      let animationLeftName = "slide-left-PC";

      let animationRightName =
        window.innerWidth >= 768 ? "slide-right-PC" : "slide-right-MB";
      let animationName = this.$fixPlayer.attr("class").includes("right")
        ? animationLeftName
        : animationRightName;
      this.$fixPlayer.removeClass().addClass(animationName);

      if (this.$fixPlayer.attr("class").includes("right")) {
        this.$fixPlayerClose.find(".icon-close").removeClass("hidden");
        this.$fixPlayerClose.find(".icon-navigate-next").addClass("hidden");
        this.isTicking = false;
      } else {
        this.isTicking = true;
      }
    });
  }
  //  滚动事件
  on_scroll() {
    this.isTicking = true;
    $(window).on("scroll", () => {
      if (!this.isTicking) {
        window.requestAnimationFrame(() => {
          setTimeout(() => {
            this.$fixPlayer.removeClass().addClass("slide-left-MB");
            this.$fixPlayerClose.find(".icon-close").addClass("hidden");
            this.$fixPlayerClose
              .find(".icon-navigate-next")
              .removeClass("hidden");
            this.isTicking = false;
          }, 1000);
        });
      }
      this.isTicking = true;
    });
  }

  detect_is_ios() {
    return (
      [
        "iPad Simulator",
        "iPhone Simulator",
        "iPod Simulator",
        "iPad",
        "iPhone",
        "iPod",
      ].includes(navigator.platform) ||
      // iPad on iOS 13 detection
      (navigator.userAgent.includes("Mac") && "ontouchend" in document)
    );
  }
}
