export default class CommercialPicture {
  constructor() {
    this.$asideCommercialWrap = $(".aside-commercial-wrap");
    this.popularPicture = $(".popular-commercial-picture");
    this.init();
  }
  init() {
    this.popularPicture.map((i, el) => {
      this.produce_a_picture($(el).find(".recom-item"));
    });

    this.close_commercial();
  }
  /**
   * 创建图片模板
   * @param {*} pcImg
   * @param {*} tabImg
   * @param {*} mbImg
   * @param {*} Img
   * @param {*} title
   */
  picture(pcImg, tabImg, mbImg, title) {
    return `<picture>
              ${
                pcImg
                  ? `<source data-srcset="${pcImg}" media="(min-width: 1248px)">`
                  : ``
              }
              ${
                tabImg
                  ? `<source data-srcset="${tabImg}" media="(min-width: 768px) and (max-width:1247px)">`
                  : ``
              }
              ${
                mbImg
                  ? `<source data-srcset="${mbImg}" media="(max-width: 767px)">`
                  : ``
              }
              <img class="lazy" data-src="${
                pcImg || mbImg || mbImg
              }" width="100%" alt="${title}" />
            </picture>`;
  }

  /**
   * 单个 item 添加 picture 图片数据
   * @param {JQuery} $el - 图片 item
   */
  produce_a_picture($el) {
    let pcImg = $el.data("pc-img");
    let tabImg = $el.data("tab-img");
    let mbImg = $el.data("mb-img");
    let title = $el.data("title");

    //  创建picture 标签
    setTimeout(() => {
      $el.html(this.picture(pcImg, tabImg, mbImg, title));
      window.LazyLoad.update();

      setTimeout(() => {
        $el
          .closest(".popular-commercial-picture")
          .append(
            `<button class="close-btn"><i class="icon-close"></i></button>`
          );
      }, 150);
    }, 300);

    //  删除所有 data 属性，美化下 html 😁
    let dataArry = ["data-pc-img", "data-tab-img", "data-mb-img", "data-title"];

    $el.removeAttr(dataArry.join(" "));
    $el.css({ height: "auto" });
  }

  /**
   * 关闭广告
   */
  close_commercial() {
    this.$asideCommercialWrap.on("click", ".close-btn", (ev) => {
      let $parents = $(ev.currentTarget).closest(".aside-commercial-wrap");
      $parents.animate(
        {
          height: 0,
          opacity: 0,
        },
        {
          duration: 240,
          complete: () => {
            $parents.remove();
          },
        }
      );
    });
  }
}
