class TabsSlickComponent {
  constructor(uniqueClass) {
    this.$el = $(`.${uniqueClass}`);
    this.$contents = this.$el.find(".tabs-slick-content");
    this.init();
  }

  init() {
    this.showContent();
    this.$el.addClass("tabs-slick-inited");
    this.eventHandler();
  }

  eventHandler() {
    //  监听tab切换
    this.$el.on("change", (ev, index, lastIndex) => {
      this.showContent(index);
    });
  }

  /**
   * 显示tab对应的content
   * @param index
   */
  showContent(index = 0) {
    let $currentContent = this.$contents.eq(index);
    //  初始化richsick插件
    if (!$currentContent.data("richSlickObj")) {
      let randomClass = `tab-slick-content-${Math.ceil(
        Math.random() * 999999
      )}`;
      $currentContent.addClass(randomClass);
      $currentContent.data(
        "richSlickObj",
        this.initRichSlickPlugin(randomClass)
      );
    } else {
      $currentContent.data("richSlickObj").update();
    }

    //  处理tabs content显示动画
    if (this.$el.find(".tabs-slick-content-box .current").length === 0) {
      $currentContent.addClass("current");
    } else {
      $currentContent.addClass("tab-slick-fade-in");
      //  隐藏现在的
      this.$el.find(".tabs-slick-content-box .current").animate(
        {
          opacity: 0,
        },
        {
          duration: 300,
        }
      );
      $currentContent.animate(
        {
          opacity: 1,
        },
        {
          duration: 300,
          complete: () => {
            $currentContent
              .addClass("current")
              .removeClass("tab-slick-fade-in")
              .siblings()
              .removeClass("current");
          },
        }
      );
    }
  }

  /**
   * 获取每个面板对应的showmore配置
   * @param uniqueClass
   * @returns {{link: *, title: *}}
   */
  getShowMoreConfig(uniqueClass) {
    let $el = $(`.${uniqueClass}`).find(".show-more-btn-html");
    let title = $el.find(".title").html().trim();
    let link = $el.find(".link").html().trim();
    return {
      title,
      link,
    };
  }

  /**
   * 初始化rich-slick插件
   * @param uniqueClass
   */
  initRichSlickPlugin(uniqueClass) {
    let { title, link } = this.getShowMoreConfig(uniqueClass);
    this.$el.find(".see-more-box a").attr("href", link);

    return new window.richSlick({
      selector: `.${uniqueClass}`,
      templateSelector: ".template",
      showMoreHTML: `<div class="show-more-video"><a href="${link}">${title} <i class="icon-east"></i></a></div>`,
      responsive: [
        {
          breakpoint: 0,
          config: {
            name: "layout-3",
            imageTotalPerItem: 4,
            total: 4,
            isShowMore: false,
          },
        },
        {
          breakpoint: 768,
          config: {
            name: "default",
            imageTotalPerItem: 3,
            col: 4,
            total: 6,
          },
        },
        {
          breakpoint: 1248,
          config: {
            name: "default",
            imageTotalPerItem: 4,
            col: 3,
            total: 8,
          },
        },
      ],
    });
  }
}

export default class TabsSlick {
  constructor() {
    this.init();
  }

  init() {
    $(".tabs-slick").map((index, el) => {
      let randomId = Math.ceil(Math.random() * 99999);
      let uniqueClass = `tabs-${randomId}`;
      el.classList.add(uniqueClass);
      new TabsSlickComponent(uniqueClass);
    });
  }
}
