export default class Topics {
  constructor() {
    this.topicsCarousel = $(".topics-carousel");
    this.init();
  }

  init() {
    window.innerWidth < 768 && this.topics_slck_carousel();
  }

  topics_slck_carousel() {
    let options = {
      infinite: false,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: false,
      speed: 320,
      dots: true,
      arrows: false,
      rows: 0,
      draggable: false,
      pauseOnFocus: false,
    };

    this.topicsCarousel.slick(options);
  }
}
