export default class WebsiteShare {
  constructor() {
    this.shareBox = $(".share-wrapper");
    this.init();
  }

  init() {
    this.share_content_init();
    this.trigger_share_show();
    this.trigger_share_hide();
  }

  share_content_init() {
    this.shareBox.append('<div class="share-wrapper-mask"></div>');
  }
  /**
   * 打开分享弹框
   */
  trigger_share_show() {
    $("body").on("click", ".trigger-share-btn", (ev) => {
      let triggerName = $(ev.currentTarget).data("toggle");
      let shareUrl = $(ev.currentTarget).data("url");
      let shareTitle = $(ev.currentTarget).data("title");
      let $shareBoxName = triggerName ? $("#" + triggerName) : this.shareBox;
      $shareBoxName.addClass("shows").removeClass("hides");

      shareUrl &&
        $shareBoxName
          .find(".share-target>.a2a_kit")
          .attr("data-a2a-url", shareUrl);

      shareTitle &&
        $shareBoxName
          .find(".share-target>.a2a_kit")
          .attr("data-a2a-title", shareTitle);

      //  弹出分享框后触发
      setTimeout(() => $(window).trigger("share_show"), 120);
    });
  }
  /**
   * 关闭分享弹框
   */
  trigger_share_hide() {
    this.shareBox.on("click", ".close-btn, .share-wrapper-mask", (ev) => {
      this.shareBox.removeClass("shows").addClass("hides");
      //  关闭分享框后触发
      setTimeout(() => $(window).trigger("share_hide"), 120);
    });

    $("body").on("click", () => {
      if (!this.shareBox.hasClass("present") && window.innerWidth < 1260)
        return;
      this.shareBox.find(".close-btn").trigger("click");
    });

    $("body").on("click", ".trigger-share-btn, .share-wrapper", (ev) => {
      ev.stopPropagation();
    });
  }
}
