export default class Subscription {
  constructor() {
    this.viaEmail = $(".via-email");
    this.backOptions = $(".back-options");
    this.$getPolicy = $(".news-privacy") || $();

    this.init();
    this.judge_privacy_checkbox();
  }
  init() {
    let subBoxHFn = ($el, subBox, open) => {
      let $subEvent = $el.parents(".sub-event");
      let subBoxH = $subEvent.find(subBox).outerHeight();
      $subEvent.height(subBoxH + 2);
      open ? $subEvent.addClass("change") : $subEvent.removeClass("change");
    };

    this.viaEmail.on("click", (ev) => {
      subBoxHFn($(ev.currentTarget), ".email-subscribe", true);
    });
    this.backOptions.on("click", (ev) => {
      subBoxHFn($(ev.currentTarget), ".free-subscribe", false);
    });
  }

  /**
   * 判断用户是否勾选隐私条款
   */
  judge_privacy_checkbox() {
    $('input[type="submit"],button[type="submit"],.messenger-payload').on(
      "click",
      (ev) => {
        let $subForm = $();
        if ($(ev.currentTarget).closest("form.mc4wp-form").length) {
          $subForm = $(ev.currentTarget).closest("form.mc4wp-form");
        } else if ($(ev.currentTarget).closest(".j-sub-form").length) {
          $subForm = $(ev.currentTarget).closest(".j-sub-form");
        } else {
          return;
        }

        let $getPolicy = $subForm.find(".news-privacy");

        if ($getPolicy.length && !$getPolicy.find(".checkbox").is(":checked")) {
          $getPolicy.addClass("warning");
          return false;
        }
      }
    );

    this.$getPolicy.on("click", () => {
      if (this.$getPolicy.find(".checkbox").is(":checked")) {
        this.$getPolicy.removeClass("warning");
      }
    });
  }
}
