//  返回顶部按钮

export default class GoTop {
  constructor() {
    this.lastScrollPosition = 0;
    if (window.innerWidth < 1199) return;
    setTimeout(() => this.init(), 3600);
  }

  init() {
    $(document.body).append(
      `<button id="go-top"><i class="icon-expand-less"></i></button>`
    );

    this.scroll_top();
    this.go_top_click();
  }
  /**
   * 按钮的显示隐藏 ，与自定义畅聊绑定
   */
  scroll_top() {
    let scroll_event = () => {
      this.lastScrollPosition = $(window).scrollTop();

      if (this.lastScrollPosition > window.innerHeight * 1.2) {
        //  当自定义畅聊展开时，隐藏返回顶部按钮
        if (
          $("#js-floating-action-box .kit-follow-platform").hasClass("show")
        ) {
          $("#go-top").removeClass("show").fadeOut(20);
        } else {
          $("#go-top").addClass("show").fadeIn(300);
        }
      } else {
        $("#go-top").removeClass("show").fadeOut(300);
      }
    };
    scroll_event();
    //  滚动屏幕计算按钮的显示隐藏
    $(window).on("scroll", () =>
      window.requestAnimationFrame(() => scroll_event())
    );
  }
  /**
   * 点击按钮，返回顶部
   */
  go_top_click() {
    $("#go-top").on("click", () => {
      $("html, body").animate(
        {
          scrollTop: 0,
        },
        600,
        () => {
          $("#go-top").fadeOut();
        }
      );
    });
  }
}
