export default class ClassifyLabel {
  constructor() {
    this.classifyLabel = $(".classify-label");
    this.dropdownBtn = $(".dropdown-btn");
    this.currentLabelNameBtn = $(".current-label-name");
    this.$labelCount = this.classifyLabel.find(".label-count");

    this.init();
  }

  init() {
    if (!this.classifyLabel.length) return;
    this.mb_modal_box_init();
    this.mb_modal_show_off();
    this.pc_label_box_expand_reduce();
    this.pc_sititem_active_position();
    this.init_current_item_position($(".slider-items .item.active"));
  }

  /**
   * 移动端弹框内容初始化
   */
  mb_modal_box_init() {
    if (window.innerWidth > 768) return;
    this.$labelCount.append(`<div class="modal-mask"></div>`);
    this.$labelCount
      .children(".label-wrap")
      .append(`<button class="close-btn"><i class="icon-close"></i></button>`);
  }
  /**
   * 移动端弹框显示与隐藏
   */
  mb_modal_show_off() {
    //  显示
    this.currentLabelNameBtn.on("click", () => {
      this.$labelCount.addClass("show");
    });

    //  隐藏

    this.$labelCount.on("click", ".close-btn,.modal-mask,.item", () => {
      this.$labelCount.removeClass("show").addClass("hide");
      setTimeout(() => {
        this.$labelCount.removeClass("hide");
      }, 380);
    });
  }
  /**
   * PC 端 label box 展开收起
   */
  pc_label_box_expand_reduce() {
    this.dropdownBtn.on("click", (ev) => {
      let $el = $(ev.currentTarget);
      if ($el.hasClass("active")) {
        $el.removeClass("active");
        this.classifyLabel.removeClass("open");
        this.init_current_item_position($(".slider-items>.active"));
      } else {
        $el.addClass("active");
        this.classifyLabel.addClass("open");
      }
    });
  }
  /**
   * PC 端点击item 执行动画
   */
  pc_sititem_active_position() {
    let $item = this.classifyLabel.find(".slider-items .item");
    $item.on("click", (ev) => {
      this.init_current_item_position($(ev.currentTarget));
    });
  }
  /**
   * 当前选中标签在当前居中显示
   * @param {*} $active
   */
  init_current_item_position($active) {
    $active.addClass("active").siblings(".item").removeClass("active");
    this.currentLabelNameBtn.find(".title").html($active.find("a").text());

    if (window.innerWidth > 767 && !this.classifyLabel.hasClass("open")) {
      let countWidth = this.$labelCount.outerWidth();
      let itemWidth = $active.outerWidth();
      let ItemOffsetLeft = $active.position().left;
      let $slider = this.$labelCount.find(".slider-items");
      let sliderWidth = (itemWidth + 16) * $slider.find(".item").length;
      let transformX = 0;
      //  判断滚动方向
      if (ItemOffsetLeft > countWidth / 2) {
        //  当滚动到最后1个 item 时的情况
        if (ItemOffsetLeft + itemWidth + 20 > sliderWidth) {
          transformX = sliderWidth - countWidth + itemWidth / 2;
        } else {
          transformX = ItemOffsetLeft - countWidth / 2 + itemWidth / 2;
        }
      } else {
        transformX = 0;
      }

      $slider.css({
        transform: `translateX(-${transformX}px)`,
        transition: `transform 0.35s ease`,
      });

      setTimeout(() => {
        $slider.css({
          transition: "",
        });
      }, 360);
    }
  }
}
