export default class SparePartsFn {
  constructor() {
    this.init();
  }
  init() {
    this.banner_card_slick();
  }

  banner_card_slick() {
    setTimeout(() => {
      let $preItem = $(".ban-item.slick-active").prev(".ban-item");
      $preItem
        .find("img.lazy")
        .attr("src", $preItem.find("img.lazy").data("src"))
        .addClass("loaded");
    }, 300);
  }
}
