import TabsPlugin from "../vendor/tabs.min";

class TabsComponent {
  constructor(uniqueClass) {
    this.$el = $(`.${uniqueClass}`);
    this.$nav = this.$el.find(".tabs-mobile-nav");
    this.defaultIndex = this.$el.find(".tabs-list").data("default");
    this.defaultIndex = this.defaultIndex === undefined ? 0 : this.defaultIndex;
    this.T = new TabsPlugin({
      el: `.${uniqueClass} .tabs`,
      index: this.defaultIndex
    });
    this.init(uniqueClass);
  }

  init(uniqueClass) {
    //  处理 popover 效果
    this.popoverHandler();
    //  处理移动端得弹框
    this.modalHandler(uniqueClass);
    //  处理事件监听
    this.eventHandler();
    this.setMobileNavText(this.defaultIndex);
  }

  modalHandler(uniqueClass) {
    let $modal = $(`<div class="modal modal-${uniqueClass}" />`);
    let items = this.getTabs();
    let content = ``;
    items.map(item => {
      content += `<button data-index="${item.index}">${item.title}</button>`;
    });
    $(document.body).append($modal);
    $modal.html(`<div class="modal-dialog tabs-modal">
      <div class="modal-box">
        <div class="modal-header">
          <button class="modal-header-close"><i class="icon-close"></i></button>
        </div>
        <div class="modal-body">
          ${content}
        </div>
      </div>
    </div>
    `);
    this.M = $modal;

    this.M.find(".modal-body button").on("click", ev => {
      let index = ev.currentTarget.dataset.index;
      this.T.go(index);
      this.hideModal();
    });

    this.M.find(".modal-header-close").on("click", ev => {
      this.hideModal();
    });
  }

  hideModal() {
    this.M.animate(
      {
        opacity: 0
      },
      () => {
        this.M.hide();
      }
    );
    this.M.off("click.tabClick");
  }

  showModal(tabData) {
    this.M.show();
    //  找到当前选中项
    let current = tabData.find(item => item.current === true);
    this.setCurrentModalIem(current.index);
    this.M.animate({
      opacity: 1
    });
    this.M.on("click.tabClick", ev => {
      if (ev.target.classList.contains("modal-dialog")) {
        this.hideModal();
      }
    });
  }

  eventHandler() {
    this.T.on("change", (ev, index, lastIndex) => {
      this.setCurrentPopover(index);
      this.setCurrentModalIem(index);
      this.$el.trigger("change", [index, lastIndex]);
      //  切换文字
      this.setMobileNavText(index);
    });

    this.T.on("expandMoreBtnClick", (ev, tabData) => {
      this.showModal(tabData);
      this.$el.trigger("expandMoreBtnClick", [tabData]);
    });

    $(document).on("click", ev => {
      if (!ev.target.closest(".tabs-component")) {
        //  隐藏popover-box
        this.hidePopover();
      }
    });

    this.$nav.on("click", () => {
      this.$el.trigger("customExpandMoreBtnClick");
    });

    //  监听自定义expandMoreBtnClick事件
    this.$el.on("customExpandMoreBtnClick", () => {
      let tabData = [];
      this.$el.find("ul li").map((key, el) => {
        tabData.push({
          current: el.classList.contains("current"),
          html: el.innerHTML,
          index: key
        });
      });
      this.showModal(tabData);
    });

    let resize = () => {
      if (parseInt(this.M.css("opacity")) === 1 && window.innerWidth > 768) {
        this.hideModal();
      }

      if (this.$el.hasClass("popover") && window.innerWidth < 768) {
        this.hidePopover();
      }

      if (this.$el.find(".tabs").hasClass("tabs-overflow")) {
        this.$el.find(".popover-btn").show()
      }else{
        this.$el.find(".popover-btn").hide()
      }
    };

    $(window).on("resize", this.debounce(resize, 200));
    resize();
  }

  /**
   * 切换移动端mobile nav按钮上的文字
   * @param index
   */
  setMobileNavText(index = 0) {
    //  找到对应li
    let $li = this.$el
      .find(".tabs-list")
      .children("li")
      .eq(index);
    this.$nav.html($li.html());
  }

  popoverHandler() {
    //  往后面添加一个icon
    this.$el.append(
      `<button class="btn-icon popover-btn"><i class="icon-more-horiz"></i></button>`
    );
    let $popoverBtn = this.$el.find(".popover-btn");
    $popoverBtn.on("click", () =>
      this.$el.hasClass("popover") ? this.hidePopover() : this.showPopover()
    );

    //  追加一个popover面板
    let items = this.getTabs();
    let popoverHTML = ``;
    items.map(item => {
      popoverHTML += `<div class="col-4 popover-item"><button class="btn line-btn" data-index="${item.index}">${item.title}</button></div>`;
    });
    this.$el.append(
      $(`<div class="popover-box" />`).html(
        `<div class="row">${popoverHTML}</div>`
      )
    );
    this.$popoverBox = this.$el.find(".popover-box");

    //  处理点击事件
    this.$popoverBox.find(".popover-item .btn").on("click", ev => {
      let index = ev.currentTarget.dataset.index;
      this.T.go(index);
    });
  }

  getTabs() {
    let items = [];
    this.$el
      .find(".tabs-list")
      .children("li")
      .map((key, item) => {
        items.push({
          index: item.dataset.index,
          title: item.innerHTML
        });
      });
    return items;
  }

  hidePopover() {
    this.$el.removeClass("popover");
    this.$popoverBox.fadeOut();
  }

  showPopover() {
    this.$el.addClass("popover");
    //  选中当前元素
    //  找到当前选中
    let currentIndex = this.$el.find(".tabs-list .current").data("index");
    this.setCurrentPopover(currentIndex);
    this.$popoverBox.fadeIn();
  }

  setCurrentModalIem(index) {
    this.M.find(".modal-body button")
      .eq(index)
      .addClass("current")
      .siblings()
      .removeClass("current");
  }

  setCurrentPopover(index) {
    this.$popoverBox
      .find(".popover-item")
      .eq(index)
      .addClass("current")
      .siblings()
      .removeClass("current");
  }

  debounce(fn, delay) {
    delay = delay || 100;
    return () => {
      let args = Array.prototype.slice.apply(arguments);
      clearTimeout(fn.debounceTimeout);
      fn.debounceTimeout = setTimeout(() => {
        fn.apply(window, args);
      }, delay);
    };
  }
}

export default class Tabs {
  constructor() {
    this.init();
  }

  init() {
    $(".tabs-component").map((index, el) => {
      let randomId = Math.ceil(Math.random() * 99999);
      let uniqueClass = `tabs-${randomId}`;
      el.classList.add(uniqueClass);
      new TabsComponent(uniqueClass);
    });
  }
}
