export default class exhibitionApps {
  constructor() {
    this.headOperate = $("#web-header .head-operate");
    this.$getAppBtn = $("#web-header .get-apps");
    this.appsItemLength = 0;
    this.init();
  }

  init() {
    this.get_apps_show();
    this.get_apps_hide();
  }
  /**
   * 显示下载app 框
   */
  get_apps_show() {
    this.$getAppBtn.on("click", (ev) => {
      let $el = $(ev.currentTarget);
      this.appsItemLength < 1 && this.create_apps_structure();
      $(ev.currentTarget).hasClass("open")
        ? this.hide_apps_box()
        : this.show_apps_box();

      return false;
    });
  }
  /**
   * 隐藏下载app 框
   */
  get_apps_hide() {
    $(document).on("click", () => {
      this.hide_apps_box();
    });

    $(document).on("click", ".exhibition-apps", () => {
      ev = ev || window.event;
      ev.stopPropagation();
    });
  }
  /**
   * 创建apps 下载盒子模板
   */
  create_apps_structure() {
    let $div = $("<div/>");
    let appsItemHTML = "";
    let $noscriptHtml = $(".head-operate noscript").html();
    $div.append($noscriptHtml);
    $div.find("a").map((i, el) => {
      appsItemHTML += `<div class="apps-item">
                          <div class="apps-sign">
                            <a href="${$(el).attr(
                              "href"
                            )}" class="symbol d-flex align-items-center" target="_blank">
                              <img src="${$(el).data("logo")}" 
                              alt="${$(el).data("title")}">
                              <p class="title">${$(el).data("title")}</p>
                            </a>
                          </div>
                          <div class="apps-store d-flex flex-wrap">
                            <a href="${$(el).data(
                              "apple-link"
                            )}" class="badge Apple" target="_blank">
                              <img src="${$(el).data("apple-img")}" alt="Apple">
                            </a>
                            <a href="${$(el).data(
                              "google-link"
                            )}" class="badge Google" target="_blank">
                              <img src="${$(el).data(
                                "google-img"
                              )}" alt="Google">
                            </a>
                          </div>
                      </div>`;
    });

    let appsBoxHTML = `<div class="exhibition-apps">
                          <div class="apps-container">
                            <div class="apps-carousel">${appsItemHTML}</div>
                          </div>
                      </div>`;

    this.appsItemLength = $div.find("a").length;
    this.headOperate.append(appsBoxHTML);
    this.appsItemLength > 1 && this.init_slick();
  }
  /**
   * 初始化轮播
   */
  init_slick() {
    let options = {
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: true,
      dots: true,
      arrows: true,
      rows: 0,
      draggable: true,
      rtl: false,
    };

    setTimeout(() => {
      this.headOperate.find(".apps-carousel").slick(options);
    }, 420);
  }
  /**
   * APP 盒子显示
   */
  show_apps_box() {
    $(".exhibition-apps").addClass("show");
    $(".exhibition-apps").fadeIn(200);
    this.$getAppBtn.addClass("open");
  }
  /**
   * APP 盒子隐藏
   */
  hide_apps_box() {
    $(".exhibition-apps").removeClass("show");
    $(".exhibition-apps").fadeOut(200);
    this.$getAppBtn.removeClass("open");
  }
}
