export default class FormSearch {
  constructor() {
    this.$searchForm = $(".search-form");
    this.init();
  }

  init() {
    this.search_input_change();
  }
  /**
   *
   * @param {*} $searchChangeBtn  - 切换搜索按钮
   * @param {*} $form  - 搜索表单
   * @param {*} $input  - 搜索框
   * @param {*} type - 默认搜素类型
   */
  init_search($searchChangeBtn, $form, $input, type) {
    let search_action_fn = (type, name, action) => {
      $form.attr("data-type", type);
      $input.attr("name", name);
      $form.attr("action", $form.attr(`data-${action}`));
    };

    if (type === "site") {
      search_action_fn("site", "s", "localhost");
    } else if (type === "google") {
      search_action_fn("google", "q", "google");
    }

    $searchChangeBtn
      .find("span")
      .html($(`button[data-option="${type}"]`).html());
  }

  /**
   * 搜索平台切换
   */
  search_input_change() {
    let timer = null;
    this.$searchForm.map((i, form) => {
      let $inputPlate = $(form).find(".input-plate");
      let $input = $(form).find("input");
      let $clearBtn = $(form).find(".clear-btn");
      let $searchBtn = $(form).find(".search-btn");
      let $searchChangeBtn = $(form).find(".search-change-btn");
      let $searchOptions = $(form).find(".search-options");
      let defType = $(form).data("type");

      this.init_search($searchChangeBtn, $(form), $input, defType);
      this.clear_input_value($clearBtn, $input, $searchBtn);
      $inputPlate.on("click", (ev) => $input.focus());
      //  如果没有google，则不往下执行
      if (typeof $(form).attr("no-gcse") !== "undefined") return;

      $(`button[data-option="${defType}"]`).addClass("active");
      $searchChangeBtn.on("click", (ev) => {
        ev.preventDefault();
        ev.stopPropagation();
        $searchOptions.addClass("show");
      });

      $searchOptions.find(".opt-item").on("click", (ev) => {
        $searchOptions.removeClass("show");

        this.search_event(
          $(ev.currentTarget),
          $(form),
          $searchChangeBtn,
          $input
        );

        clearTimeout(timer);
        timer = setTimeout(() => {
          setTimeout(() => $input.focus(), 200);
        });
      });
      this.init_check_input_value($input, $clearBtn);
      this.search_options_box_hide($searchOptions, $input);
    });
  }
  /**
   * 搜索项盒子隐藏操作
   * @param {*} $searchOptions - 搜索项盒子
   */
  search_options_box_hide($searchOptions, $input) {
    $(document).on("click", () => {
      $searchOptions.removeClass("show");
    });

    $searchOptions.on("click", () => {
      if ($input.get(0).value.trim() === "") return false;
    });
  }

  /**
   * 搜索平台切换参数配置
   */
  search_event($el, $form, $searchChangeBtn, $input) {
    let type = $el.attr("data-option");
    this.init_search($searchChangeBtn, $form, $input, type);

    setTimeout(() => {
      $el.addClass("active").siblings("button").removeClass("active");
    }, 150);
  }

  /**
   * 搜索框的各种操作：回车搜索、清空输入框、判断输入空格....
   */

  clear_input_value($clearBtn, $input, $searchBtn) {
    //  回车搜索
    $input.on("keydown", (ev) => {
      let el = ev.currentTarget;
      if (ev.keyCode === 13) {
        if (el.value.trim() === "") return false;
      }
      $clearBtn.addClass("on");
    });

    $input.on("keyup", (ev) => {
      if (ev.currentTarget.value === "") {
        $clearBtn.removeClass("on");
      }
    });

    //  按钮搜索
    $searchBtn.on("click", () => {
      if ($input.get(0).value.trim() === "") return false;
    });
    //  清空搜索框
    $clearBtn.on("mousedown", (ev) => {
      $(ev.currentTarget).removeClass("on");
      $input.val("");
      return false;
    });
  }
  /**
   * 检测加载进来输入框不为空时显示清除按钮
   */

  init_check_input_value($input, $clearBtn) {
    if ($input.get(0).value.trim() !== "") {
      $clearBtn.addClass("on");
    }
  }
}
