export default class CardCarousel {
  constructor() {
    this.init();
  }

  init() {}
}

window.slickCarousel = (showName, toShow, toScroll) => {
  let $mySlick = $(showName);

  let options = {
    infinite: false,
    slidesToShow: toShow,
    slidesToScroll: toScroll,
    adaptiveHeight: false,
    speed: 320,
    dots: false,
    arrows: true,
    rows: 0,
    draggable: false,
    pauseOnFocus: false,
    responsive: [
      {
        breakpoint: 1260,
        settings: "unslick",
      },
    ],
  };

  $mySlick.slick(options);
  slick_arrow_btn_control($mySlick);
  slick_revival($mySlick, options);
  limit_slick_item_width($mySlick, toShow);
  setTimeout(() => window.LazyLoad.update(), 100);
};

const limit_slick_item_width = ($el, toShow) => {
  if (toShow === 3) {
    $el.addClass("show-3");
  } else if (toShow == 4) {
    $el.addClass("show-4");
  } else if (toShow == 5) {
    $el.addClass("show-5");
  }
};

const slick_revival = ($mySlick, options) => {
  $mySlick.on("destroy", (ev, slick) => {
    //  @ts-ignore;
    ev.currentTarget.dataset.slickInit = false;
  });

  $mySlick.map((k, item) => {
    //  @ts-ignore;
    item.dataset.slickInit = false;
  });

  $(window).on("resize orientationchange", () => {
    if (window.innerWidth > 1259) {
      $mySlick.map((k, el) => {
        if (
          !~el.className.indexOf("slick-initialized") &&
          el.dataset.slickInit == "false"
        ) {
          $(el).slick(options);
          //  @ts-ignore;
          el.dataset.slickInit = true;
        }
      });
    }
  });
};

const slick_arrow_btn_control = ($mySlick) => {
  $mySlick.on("beforeChange", (event, slick, currentSlide, nextSlide) => {
    if (nextSlide === (Math.ceil(slick.slideCount / 3) - 1) * 4) {
      slick.$prevArrow.show();
      slick.$nextArrow.hide();
    } else if (nextSlide === 0) {
      slick.$prevArrow.hide();
      slick.$nextArrow.show();
    } else {
      slick.$prevArrow.show();
      slick.$nextArrow.show();
    }
  });
};

window.slickCarousel(".carousel1", 4, 4);
