const FlexibleOption = (elDom, row1, row2) => {
  return {
    selector: elDom,
    rows: 3,
    titleSelector: ".card-title>p>a",
    descriptionSelector: ".card-title>.desc",
    responsive: [
      {
        breakpoint: 1248,
        rows: row1,
      },
      {
        breakpoint: 768,
        rows: row2,
      },
    ],
  };
};
export default class Lists {
  constructor() {
    this.init();
  }
  init() {
    this.default_list_rows();
    this.has_attrs_list_rows();
  }

  default_list_rows() {
    new window.FlexibleDescription(
      FlexibleOption(".list-card:not(.has-attrs)", 5, 4)
    );
  }

  has_attrs_list_rows() {
    new window.FlexibleDescription(
      FlexibleOption(".list-card.has-attrs", 4, 3)
    );
  }
}

window.flexible_description_reset = () => {
  //  @ts-ignore
  new window.FlexibleDescription(FlexibleOption(".list-card", 5, 4));
};
